$ruler: 16px;
$color-red: #AE1100;
$color-bg: #EBECF0;
$color-shadow: #BABECC;
$color-white: #FFF;

.navbarCont {
    z-index: 10;
    position: relative;
    box-sizing: border-box;
    box-shadow: -5px -5px 20px $color-white,  5px 5px 20px $color-shadow;
    width: 100%;
    padding: 0px 30px 30px 30px;
    background-color: #EBECF0;
}

.link-title {
  text-decoration: none;
}

.pageTitle {
    z-index: 6;
    position: relative;
    font-family: "Trebuchet MS", Helvetica, sans-serif;
    list-style-type: none;
    width: 4em;
    font-weight: 600;
    font-size: 28px;
    color: #f75422af;
    top: 0.92em;
    left: 2em;
    
}

.logout-btn {
  color: #E96255 !important;
  transition: color 0.3s ease;
}

.logout-btn:hover {
  color: red !important;
}

// ===============================================

.cb-hidden {
  position: absolute;
  height: 1px;
  width: 1px;
  clip: rect(1px,1px,1px,1px);
  border: 0;
  overflow: hidden
}

.label-cb {
  display: none;
}

#menuToggle {
  display: block;
  position: relative;
  top: 0px;
  left: 0px;
  z-index: 1;
  -webkit-user-select: none;
  user-select: none;
}

#menuToggle a {
  text-decoration: none;
  color: #232323;
  transition: color 0.5s ease;
}

#menuToggle a:hover {
  color: #f75422af;
}


#menuToggle input {
  display: block;
  width: 30px;
  height: 32px;
  position: absolute;
  top: -7px;
  left: -5px;
  
  cursor: pointer;
  
  opacity: 0; /* hide this */
  z-index: 2; /* and place it over the hamburger */
  
  -webkit-touch-callout: none;
}

#menuToggle span {
  display: block;
  width: 33px;
  height: 4px;
  margin-bottom: 5px;
  position: relative;
  
  background: black;
  border-radius: 3px;
  
  z-index: 1;
  
  transform-origin: 4px 0px;
  
  transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
              background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
              opacity 0.55s ease;
}

#menuToggle span:first-child {
  transform-origin: 0% 0%;
}

#menuToggle span:nth-last-child(2) {
  transform-origin: 0% 100%;
}

#menuToggle input:checked ~ span {
  opacity: 1;
  transform: rotate(45deg) translate(-2px, -1px);
  background: #232323;
}

#menuToggle input:checked ~ span:nth-last-child(3){
  opacity: 0;
  transform: rotate(0deg) scale(0.2, 0.2);
}

#menuToggle input:checked ~ span:nth-last-child(2){
  transform: rotate(-45deg) translate(0, -1px);
}

#menu {
  position: absolute;
  width: 14em;
  margin: -100px 0 0 -50px;
  padding: 50px;
  padding-top: 125px;
  
  background:#EBECF0;
  list-style-type: none;
  -webkit-font-smoothing: antialiased;  
  transform-origin: 0% 0%;
  transform: translate(-100%, 0);
  
  transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
}

#menu div {
  position: relative;
  padding: 10px 0;
  font-size: 22px;
  text-align: left;
  left: 2em;
}

#menuToggle input:checked ~ div {
  transform: none;
}

@media only screen and (max-width: 608px){
  #menu {
    width: 95%;
  }
}